import { observable, action } from 'mobx'
import {
  REQUEST_STATE,
  UNLIMITED_TOWER_CONTRACT,
  UNLIMITED_NFT_CONTRACT,
  UNLIMITED_SERVANT_TABLE,
  UNLIMITED_ITEM_TABLE,
  UNLIMITED_MONSTER_TABLE,
  NFT_SERVANT_TABLE,
  NFT_MONSTER_TABLE,
  NFT_ITEM_TABLE,
  API_PRODUCTS,
  API_MY_HISTORIES,
  UTS_KEY,
  UTM_KEY,
  UTI_KEY,
} from '../../../../constants/Values'
import request from '../../../../utils/request'

export default (eosioStore, authenticationStore) =>
  observable(
    {
      state: REQUEST_STATE.DONE,
      activeKey: UTS_KEY,
      nftServants: {
        contents: [],
        page: 1,
        pageSize: 10,
        totalElements: 0,
        totalPages: 0,
      },
      nftMonsters: {
        contents: [],
        page: 1,
        pageSize: 10,
        totalElements: 0,
        totalPages: 0,
      },
      nftItems: {
        contents: [],
        page: 1,
        pageSize: 10,
        totalElements: 0,
        totalPages: 0,
      },

      async getNftServants(page) {
        if (!authenticationStore.accountInfo) {
          return
        }
        const accountName = authenticationStore.accountInfo.account_name
        // const accountName = 'dlwodnjs1111'

        this.state = REQUEST_STATE.LOADING

        try {
          // get total size
          const total = await getNftTableCount(eosioStore, accountName, UNLIMITED_NFT_CONTRACT, NFT_SERVANT_TABLE)

          this.nftServants.totalElements = total
          this.nftServants.totalPages =
            total / this.nftServants.pageSize + (total % this.nftServants.pageSize > 0 ? 1 : 0)
          // get with paging

          if (page) {
            this.nftServants.page = page
          }

          const result = await getNftTableData(
            eosioStore,
            accountName,
            UNLIMITED_NFT_CONTRACT,
            NFT_SERVANT_TABLE,
            1000
            //this.nftServants.pageSize,
            //this.nftServants.totalElements - (this.nftServants.page - 1) * this.nftServants.pageSize
          )

          this.nftServants.contents = await Promise.all(
            result.rows.map(async (item, idx) => {
              const result = await getTableData(
                eosioStore,
                accountName,
                UNLIMITED_TOWER_CONTRACT,
                UNLIMITED_SERVANT_TABLE,
                1,
                item.t_idx
              )

              const res = await request.get(`${API_PRODUCTS}/uts/${result.rows[0].id}`)
              let tradeResult

              if (item.state === 'selling') {
                tradeResult = await request.get(`${API_MY_HISTORIES}/${accountName}/uts/${item.idx}`)
              }

              return {
                ...item,
                ...result.rows[0],
                ...res.data,
                trade: tradeResult ? tradeResult.data : undefined,
              }
            })
          )

          const claimUts = await request.get(`${API_MY_HISTORIES}/${accountName}/uts/claims`)
          const utsList = claimUts.data.map((item, idx) => {
            return {
              imageUrl: item.nft.gameInfo.imageUrl,
              name: item.nft.gameInfo.name,
              tier: 0,
              level: item.nft.gameInfo.servantLevel,
              grade: 0,
              state: item.state,
              trade: item,
            }
          })

          this.state = REQUEST_STATE.DONE
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
          return err
        }
      },
      async getNftItems(page) {
        if (!authenticationStore.accountInfo) {
          return
        }
        const accountName = authenticationStore.accountInfo.account_name
        // const accountName = 'dlwodnjs1111'

        this.state = REQUEST_STATE.LOADING

        try {
          // get total size
          const total = await getNftTableCount(eosioStore, accountName, UNLIMITED_NFT_CONTRACT, NFT_ITEM_TABLE)

          this.nftItems.totalElements = total
          this.nftItems.totalPages = total / this.nftItems.pageSize + (total % this.nftItems.pageSize > 0 ? 1 : 0)
          // get with paging

          if (page) {
            this.nftItems.page = page
          }

          const result = await getNftTableData(
            eosioStore,
            accountName,
            UNLIMITED_NFT_CONTRACT,
            NFT_ITEM_TABLE,
            100
            //this.nftItems.pageSize,
            //this.nftItems.totalElements - (this.nftItems.page - 1) * this.nftItems.pageSize
          )

          this.nftItems.contents = await Promise.all(
            result.rows.map(async (item, idx) => {
              const result = await getTableData(
                eosioStore,
                accountName,
                UNLIMITED_TOWER_CONTRACT,
                UNLIMITED_ITEM_TABLE,
                1,
                item.t_idx
              )

              const res = await request.get(`${API_PRODUCTS}/uti/${result.rows[0].id}`)
              let tradeResult

              if (item.state === 'selling') {
                tradeResult = await request.get(`${API_MY_HISTORIES}/${accountName}/uti/${item.idx}`)
              }

              return {
                ...item,
                ...result.rows[0],
                ...res.data,
                trade: tradeResult ? tradeResult.data : undefined,
              }
            })
          )

          this.state = REQUEST_STATE.DONE
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
          return err
        }
      },
      async getNftMonsters(page) {
        if (!authenticationStore.accountInfo) {
          return
        }
        const accountName = authenticationStore.accountInfo.account_name
        // const accountName = 'dlwodnjs1111'

        this.state = REQUEST_STATE.LOADING

        try {
          // get total size
          const total = await getNftTableCount(eosioStore, accountName, UNLIMITED_NFT_CONTRACT, NFT_MONSTER_TABLE)

          this.nftMonsters.totalElements = total
          this.nftMonsters.totalPages =
            total / this.nftMonsters.pageSize + (total % this.nftMonsters.pageSize > 0 ? 1 : 0)
          // get with paging

          if (page) {
            this.nftMonsters.page = page
          }

          const result = await getNftTableData(
            eosioStore,
            accountName,
            UNLIMITED_NFT_CONTRACT,
            NFT_MONSTER_TABLE,
            100
            //this.nftMonsters.pageSize,
            //this.nftMonsters.totalElements - (this.nftMonsters.page - 1) * this.nftMonsters.pageSize
          )

          this.nftMonsters.contents = await Promise.all(
            result.rows.map(async (item, idx) => {
              const result = await getTableData(
                eosioStore,
                accountName,
                UNLIMITED_TOWER_CONTRACT,
                UNLIMITED_MONSTER_TABLE,
                1,
                item.t_idx
              )

              const res = await request.get(`${API_PRODUCTS}/utm/${result.rows[0].id}`)
              let tradeResult

              if (item.state === 'selling') {
                tradeResult = await request.get(`${API_MY_HISTORIES}/${accountName}/utm/${item.idx}`)
              }

              return {
                ...item,
                ...result.rows[0],
                ...res.data,
                trade: tradeResult ? tradeResult.data : undefined,
              }
            })
          )

          this.state = REQUEST_STATE.DONE
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
          return err
        }
      },
      async backToGame(item, type) {
        if (!authenticationStore.accountInfo) {
          return
        }
        const accountName = authenticationStore.accountInfo.account_name
        // const accountName = 'autumnautumn'

        this.state = REQUEST_STATE.LOADING

        try {
          const cb = tr => {
            const options = { authorization: [`${accountName}@active`] }

            tr.backtogame(
              {
                from: accountName,
                sym: type,
                id: item.idx,
              },
              options
            )
          }

          await eosioStore.createTransactionWithContract(UNLIMITED_NFT_CONTRACT, cb)

          if (type === UTS_KEY) {
            await this.getNftServants(1)
          } else if (type === UTM_KEY) {
            await this.getNftMonsters(1)
          } else if (type === UTI_KEY) {
            await this.getNftItems(1)
          }

          this.state = REQUEST_STATE.DONE

          return true
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
          return err
        }
      },
      async regAuction(id, minPrice, maxPrice, type) {
        if (!authenticationStore.accountInfo) {
          return
        }

        const accountName = authenticationStore.accountInfo.account_name

        this.state = REQUEST_STATE.LOADING

        try {
          const cb = tr => {
            const options = { authorization: [`${accountName}@active`] }

            tr.regauction(
              {
                seller: accountName,
                sym: type,
                token_id: id,
                min_price: `${minPrice.toFixed(4)} EOS`,
                max_price: `${maxPrice.toFixed(4)} EOS`,
              },
              options
            )
          }

          await eosioStore.createTransactionWithContract(UNLIMITED_NFT_CONTRACT, cb)

          if (type === UTS_KEY) {
            await this.getNftServants(1)
          } else if (type === UTM_KEY) {
            await this.getNftMonsters(1)
          } else if (type === UTI_KEY) {
            await this.getNftItems(1)
          }

          this.state = REQUEST_STATE.DONE

          return true
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
          return err
        }
      },
      async claimNft(nft, type) {
        if (!authenticationStore.accountInfo) {
          return
        }

        const accountName = authenticationStore.accountInfo.account_name

        this.state = REQUEST_STATE.LOADING

        try {
          const cb = tr => {
            const options = { authorization: [`${accountName}@active`] }

            tr.claim(
              {
                buyer: accountName,
                sym: type,
                trade_id: nft.id,
              },
              options
            )
          }

          await eosioStore.createTransactionWithContract(UNLIMITED_NFT_CONTRACT, cb)

          if (type === UTS_KEY) {
            await this.getNftServants(1)
          } else if (type === UTM_KEY) {
            await this.getNftMonsters(1)
          } else if (type === UTI_KEY) {
            await this.getNftItems(1)
          }

          this.state = REQUEST_STATE.DONE

          return true
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
          return err
        }
      },

      async cancelTrade(tradeId, type) {
        if (!authenticationStore.accountInfo) {
          return
        }

        const accountName = authenticationStore.accountInfo.account_name

        this.state = REQUEST_STATE.LOADING

        try {
          const cb = tr => {
            const options = { authorization: [`${accountName}@active`] }

            tr.cancelauc(
              {
                seller: accountName,
                trade_id: tradeId,
              },
              options
            )
          }

          await eosioStore.createTransactionWithContract(UNLIMITED_NFT_CONTRACT, cb)

          if (type === UTS_KEY) {
            await this.getNftServants(1)
          } else if (type === UTM_KEY) {
            await this.getNftMonsters(1)
          } else if (type === UTI_KEY) {
            await this.getNftItems(1)
          }

          this.state = REQUEST_STATE.DONE

          return true
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
          return err
        }
      },

      setActiveKey(activeKey) {
        this.activeKey = activeKey
      },
    },
    {
      getNftServants: action,
      getNftMonsters: action,
      getNftItems: action,
      backToGame: action,
      regAuction: action,
      claimNft: action,
      cancelTrade: action,
      setActiveKey: action,
    }
  )

const getNftTableCount = async (eosioStore, accountName, contract, table) => {
  const query = {
    json: true,
    code: contract,
    scope: accountName,
    table: table,
    limit: 1,
    reverse: true,
  }

  const result = await eosioStore.getTableRows(query)
  return result.rows && result.rows[0] ? result.rows[0].idx + 1 : 0
}

const getTableData = async (eosioStore, accountName, contract, table, limit, upperBound) => {
  const query = {
    json: true,
    code: contract,
    scope: accountName,
    table: table,
    limit: limit,
    reverse: true,
    upper_bound: upperBound,
  }

  return await eosioStore.getTableRows(query)
}

const getNftTableData = async (eosioStore, accountName, contract, table, limit) => {
  const query = {
    json: true,
    code: contract,
    scope: contract,
    table: table,
    table_key: 'byowner',
    key_type: 'name',
    index_position: 2,
    limit: limit,
    reverse: true,
    upper_bound: accountName,
    lower_bound: accountName,
  }

  return await eosioStore.getTableRows(query)
}
