import { observable, action } from 'mobx'
import {
  REQUEST_STATE,
  UNLIMITED_TOWER_CONTRACT,
  UNLIMITED_NFT_CONTRACT,
  UNLIMITED_GOLD_TABLE,
  UNLIMITED_SERVANT_TABLE,
  UNLIMITED_ITEM_TABLE,
  UNLIMITED_MONSTER_TABLE,
  NFT_SERVANT_TABLE,
  NFT_MONSTER_TABLE,
  NFT_ITEM_TABLE,
} from '../../../../constants/Values'

export default (eosioStore, authenticationStore) =>
  observable(
    {
      state: REQUEST_STATE.DONE,
      utg: {
        key: '1',
        img: '/img/utg.png',
        name: 'UTG',
        staking: 0,
        available: 0,
      },
      uts: {
        key: '2',
        img: '/img/uts.png',
        name: 'UTS',
        staking: 0,
        available: 0,
      },
      uti: {
        key: '3',
        img: '/img/uti.png',
        name: 'UTI',
        staking: 0,
        available: 0,
      },
      utm: {
        key: '4',
        img: '/img/utm.png',
        name: 'UTM',
        staking: 0,
        available: 0,
      },

      async getMyAccountData() {
        if (!authenticationStore.accountInfo) {
          return
        }

        this.state = REQUEST_STATE.LOADING
        const accountName = authenticationStore.accountInfo.account_name
        // const accountName = 'autumnautumn'
        const nftAccountName = authenticationStore.accountInfo.account_name

        try {
          // {"rows":[{"balance":"97000.0000 UTG"}],"more":false}
          this.utg.available = await getUtgCount(eosioStore, accountName)

          this.uts.available = await getNftTableCount(
            eosioStore,
            nftAccountName,
            UNLIMITED_NFT_CONTRACT,
            NFT_SERVANT_TABLE
          )
          this.uti.available = await getNftTableCount(
            eosioStore,
            nftAccountName,
            UNLIMITED_NFT_CONTRACT,
            NFT_ITEM_TABLE
          )
          this.utm.available = await getNftTableCount(
            eosioStore,
            nftAccountName,
            UNLIMITED_NFT_CONTRACT,
            NFT_MONSTER_TABLE
          )

          this.uts.staking =
            (await getStakingTableCount(eosioStore, accountName, UNLIMITED_TOWER_CONTRACT, UNLIMITED_SERVANT_TABLE)) -
            this.uts.available
          this.uti.staking =
            (await getStakingTableCount(eosioStore, accountName, UNLIMITED_TOWER_CONTRACT, UNLIMITED_ITEM_TABLE)) -
            this.uti.available
          this.utm.staking =
            (await getStakingTableCount(eosioStore, accountName, UNLIMITED_TOWER_CONTRACT, UNLIMITED_MONSTER_TABLE)) -
            this.utm.available

          this.state = REQUEST_STATE.DONE
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
        }
      },
    },
    {
      getMyAccountData: action,
    }
  )

const getUtgCount = async (eosioStore, accountName) => {
  const utgQuery = {
    json: true,
    code: UNLIMITED_TOWER_CONTRACT,
    scope: accountName,
    table: UNLIMITED_GOLD_TABLE,
  }

  const unlimitedUtg = await eosioStore.getTableRows(utgQuery)
  // {"rows":[{"balance":"97000.0000 UTG"}],"more":false}
  return unlimitedUtg.rows && unlimitedUtg.rows[0] ? parseFloat(unlimitedUtg.rows[0].balance.split(' ')[0]) : 0
}

const getStakingTableCount = async (eosioStore, accountName, contracct, table) => {
  const query = {
    json: true,
    code: contracct,
    scope: accountName,
    table: table,
    limit: 1,
    reverse: true,
  }

  const result = await eosioStore.getTableRows(query)
  return result.rows && result.rows[0] ? result.rows[0].index : 0
}

const getNftTableCount = async (eosioStore, accountName, contract, table) => {
  const query = {
    json: true,
    code: contract,
    scope: contract,
    table: table,
    key: 'byowner',
    key_type: 'i64',
    index_position: 2,
    upper_bound: accountName,
    lower_bound: accountName,
  }

  const result = await eosioStore.getTableRows(query)
  return result.rows && result.rows[0] ? result.rows[0].idx + 1 : 0
}
