import { observable, action } from 'mobx'
import {
  REQUEST_STATE,
  UNLIMITED_TOWER_CONTRACT,
  UNLIMITED_NFT_CONTRACT,
  UNLIMITED_SERVANT_TABLE,
  UNLIMITED_ITEM_TABLE,
  UNLIMITED_MONSTER_TABLE,
  NFT_SERVANT_TABLE,
  NFT_MONSTER_TABLE,
  NFT_ITEM_TABLE,
  API_PRODUCTS,
  UTS_KEY,
  UTM_KEY,
  UTI_KEY,
} from '../../../../constants/Values'
import request from '../../../../utils/request'

export default (eosioStore, authenticationStore) =>
  observable(
    {
      state: REQUEST_STATE.DONE,
      activeKey: UTS_KEY,

      servants: {
        contents: [],
        page: 1,
        pageSize: 10,
        totalElements: 0,
        totalPages: 0,
      },
      monsters: {
        contents: [],
        page: 1,
        pageSize: 10,
        totalElements: 0,
        totalPages: 0,
      },
      items: {
        contents: [],
        page: 1,
        pageSize: 10,
        totalElements: 0,
        totalPages: 0,
      },

      async getServants(page) {
        if (!authenticationStore.accountInfo) {
          return
        }
        const accountName = authenticationStore.accountInfo.account_name
        // const accountName = 'autumnautumn'

        this.state = REQUEST_STATE.LOADING

        try {
          // get total size
          const total = await getStakingTableCount(
            eosioStore,
            accountName,
            UNLIMITED_TOWER_CONTRACT,
            UNLIMITED_SERVANT_TABLE
          )

          this.servants.totalElements = total
          this.servants.totalPages = total / this.servants.pageSize + (total % this.servants.pageSize > 0 ? 1 : 0)
          // get with paging

          if (page) {
            this.servants.page = page
          }

          let result = await getTableData(
            eosioStore,
            accountName,
            UNLIMITED_TOWER_CONTRACT,
            UNLIMITED_SERVANT_TABLE,
            this.servants.pageSize,
            this.servants.totalElements - (this.servants.page - 1) * this.servants.pageSize
          )

          const nftResult = await getNftTableData(eosioStore, accountName, UNLIMITED_NFT_CONTRACT, NFT_SERVANT_TABLE)

          if (nftResult.rows.length > 0) {
            result.rows = result.rows.filter(item => {
              const t = nftResult.rows.filter(nft => item.index === nft.t_idx)

              return t.length === 0
            })
          }

          this.servants.contents = await Promise.all(
            result.rows.map(async (item, idx) => {
              const res = await request.get(`${API_PRODUCTS}/uts/${item.id}`)
              return {
                ...item,
                ...res.data,
              }
            })
          )

          this.state = REQUEST_STATE.DONE
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
        }
      },
      async getItems(page) {
        if (!authenticationStore.accountInfo) {
          return
        }
        const accountName = authenticationStore.accountInfo.account_name
        // const accountName = 'autumnautumn'

        this.state = REQUEST_STATE.LOADING

        try {
          // get total size
          const total = await getStakingTableCount(
            eosioStore,
            accountName,
            UNLIMITED_TOWER_CONTRACT,
            UNLIMITED_ITEM_TABLE
          )

          this.items.totalElements = total
          this.items.totalPages = total / this.items.pageSize + (total % this.items.pageSize > 0 ? 1 : 0)
          // get with paging

          if (page) {
            this.items.page = page
          }

          const result = await getTableData(
            eosioStore,
            accountName,
            UNLIMITED_TOWER_CONTRACT,
            UNLIMITED_ITEM_TABLE,
            this.items.pageSize,
            this.items.totalElements - (this.items.page - 1) * this.items.pageSize
          )

          const nftResult = await getNftTableData(eosioStore, accountName, UNLIMITED_NFT_CONTRACT, NFT_ITEM_TABLE)

          if (nftResult.rows.length > 0) {
            result.rows = result.rows.filter(item => {
              const t = nftResult.rows.filter(nft => item.index === nft.t_idx)

              return t.length === 0
            })
          }

          this.items.contents = await Promise.all(
            result.rows.map(async (item, idx) => {
              const res = await request.get(`${API_PRODUCTS}/uti/${item.id}`)
              return {
                ...item,
                ...res.data,
              }
            })
          )

          this.state = REQUEST_STATE.DONE
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
        }
      },
      async getMonsters(page) {
        if (!authenticationStore.accountInfo) {
          return
        }
        const accountName = authenticationStore.accountInfo.account_name
        // const accountName = 'autumnautumn'

        this.state = REQUEST_STATE.LOADING

        try {
          // get total size
          const total = await getStakingTableCount(
            eosioStore,
            accountName,
            UNLIMITED_TOWER_CONTRACT,
            UNLIMITED_MONSTER_TABLE
          )

          this.monsters.totalElements = total
          this.monsters.totalPages = total / this.monsters.pageSize + (total % this.monsters.pageSize > 0 ? 1 : 0)
          // get with paging

          if (page) {
            this.monsters.page = page
          }

          const result = await getTableData(
            eosioStore,
            accountName,
            UNLIMITED_TOWER_CONTRACT,
            UNLIMITED_MONSTER_TABLE,
            this.monsters.pageSize,
            this.monsters.totalElements - (this.monsters.page - 1) * this.monsters.pageSize
          )

          const nftResult = await getNftTableData(eosioStore, accountName, UNLIMITED_NFT_CONTRACT, NFT_MONSTER_TABLE)

          if (nftResult.rows.length > 0) {
            result.rows = result.rows.filter(item => {
              const t = nftResult.rows.filter(nft => item.index === nft.t_idx)

              return t.length === 0
            })
          }

          this.monsters.contents = await Promise.all(
            result.rows.map(async (item, idx) => {
              const res = await request.get(`${API_PRODUCTS}/utm/${item.id}`)
              return {
                ...item,
                ...res.data,
              }
            })
          )

          this.state = REQUEST_STATE.DONE
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
        }
      },
      async exportNft(item, type) {
        if (!authenticationStore.accountInfo) {
          return
        }
        const accountName = authenticationStore.accountInfo.account_name
        // const accountName = 'autumnautumn'

        this.state = REQUEST_STATE.LOADING

        try {
          const cb = tr => {
            const options = { authorization: [`${accountName}@active`] }

            tr.issue(
              {
                to: accountName,
                quantity: `1 ${type}`,
                index: item.index,
              },
              options
            )
          }

          await eosioStore.createTransactionWithContract(UNLIMITED_NFT_CONTRACT, cb)

          if (type === UTS_KEY) {
            await this.getServants()
          } else if (type === UTM_KEY) {
            await this.getMonsters()
          } else if (type === UTI_KEY) {
            await this.getItems()
          }

          this.state = REQUEST_STATE.DONE
          return true
        } catch (err) {
          console.error(err)
          this.state = REQUEST_STATE.ERROR
          return err
        }
      },

      setActiveKey(activeKey) {
        this.activeKey = activeKey
      },
    },
    {
      getServants: action,
      getItems: action,
      getMonsters: action,
      exportNft: action,
      setActiveKey: action,
    }
  )

const getStakingTableCount = async (eosioStore, accountName, contracct, table) => {
  const query = {
    json: true,
    code: contracct,
    scope: accountName,
    table: table,
    limit: 1,
    reverse: true,
  }

  const result = await eosioStore.getTableRows(query)
  return result.rows && result.rows[0] ? result.rows[0].index : 0
}

const getTableData = async (eosioStore, accountName, contract, table, limit, upperBound) => {
  const query = {
    json: true,
    code: contract,
    scope: accountName,
    table: table,
    limit: limit,
    reverse: true,
    upper_bound: upperBound,
  }

  return await eosioStore.getTableRows(query)
}

const getNftTableData = async (eosioStore, accountName, contract, table, limit) => {
  const query = {
    json: true,
    code: contract,
    scope: contract,
    table: table,
    table_key: 'byowner',
    key_type: 'name',
    index_position: 2,
    limit: limit,
    reverse: true,
    upper_bound: accountName,
    lower_bound: accountName,
  }

  return await eosioStore.getTableRows(query)
}
